import { useEffect, useState } from "react";
import { Table, Dialog, Button, MessagePlugin, Radio } from "tdesign-react";
import { apiTerminal, apiDeleteTerminalld } from "../../api/api";
import { Search, searchConfType } from "../../components/Search";
import { ErrorCircleFilledIcon } from "tdesign-icons-react";
import NoData from "../../components/NoData";
import { Breadcrumb } from "tdesign-react";
import moment from "moment";
const { BreadcrumbItem } = Breadcrumb;

function Terminal(props: any) {
  const [terminalPage, setTerminalPage] = useState(1);
  const [terminalPageSize, setTerminalPageSize] = useState(10);
  const [terminalData, setTerminalData] = useState([]);
  const [terminalTotal, setTerminalTotal] = useState(30);
  const [visible, setVisible] = useState(false);
  const [deleteDish, setDeleteDish] = useState({});
  const [provider, setProvider] = useState("");
  const [searchValue, setSearchValue] = useState({});

  // 搜索数据定义
  const searchConf: searchConfType[] = [
    {
      label: "设备名称:",
      key: "terminalName",
      value: "",
      callback: searchHandle,
      setValue: setSearchValue,
    },
    {
      label: "设备ID:",
      key: "terminalId",
      value: "",
      callback: searchHandle,
      setValue: setSearchValue,
    },
  ];

  // 生命周期调用数据
  useEffect(() => {
    const { provider } = props.location.state
    setProvider(provider)
    getData({provider});
  }, []);

  // 获取数据
  const getData = (items?: any) => {
    const serverId = props.match.params.id;
    if (!serverId) {
      return false;
    }
    apiTerminal({
      page: items?.terminalPage ?? terminalPage,
      pageSize: items?.terminalPageSize ?? terminalPageSize,
      terminalName: items?.terminalName,
      terminalId: items?.terminalId,
      serverId,
      provider: items?.provider ?? provider,
    }).then((res: any) => {
      const { data } = res;
      if (res.code === 0) {
        setTerminalData(data.items);
        setTerminalTotal(data.total);
      } else {
        MessagePlugin.error(res.msg ?? "请求出错！");
      }
    });
  };

  // 搜索功能
  function searchHandle(searchVal: any) {
    setTerminalPage(1);
    setTerminalPageSize(10);
    getData({ setTerminalPage: 1, setTerminalPageSize: 10, ...searchVal });
  }

  // 删除功能
  const deleteHandle = () => {
    if (deleteDish && (deleteDish as any).terminalId) {
      const {terminalId,serverId} = deleteDish as any
      apiDeleteTerminalld({id:terminalId,serverId, provider}).then(res => {
        if ((res as any).code == 0){
          setDeleteDish({});
          setVisible(false);
          MessagePlugin.success('信息删除成功！');
          getData({ ...searchValue, provider });
        } else {
          MessagePlugin.error((res as any).msg);
        }
      })   
    } else {
      MessagePlugin.error("删除请求出错了");
    }
  };

  // 关闭弹框
  const handleFooterClose = () => {
    setVisible(false);
  };

  // 更改单选类型 - 不同服务商切换
  const changeHandle = (val: any) => {
    setProvider(val);
    getData({ ...searchValue, provider: val });
  };

  return (
    <>
      <div className="BreadcrumbCont">
        <Breadcrumb maxItemWidth="200px" theme="light">
          <BreadcrumbItem href="/server">服务管理</BreadcrumbItem>
          <BreadcrumbItem>设备管理</BreadcrumbItem>
        </Breadcrumb>
      </div>
      <div className="container" key="server">
        <div className="searchCont">
          <Search searchConf={searchConf}></Search>
          <div className="tdesign-demo-block-row">
            {/* <Radio.Group value={provider} onChange={changeHandle}>
              <Radio value="BAIDU">百度地图</Radio>
              <Radio value="AMAP">高德地图</Radio>*/}
              {/* <Radio value="TMAP">腾讯地图</Radio> */}
            {/* </Radio.Group> */}
          </div>
        </div>
        {terminalData && terminalData.length > 0 ? (
          <Table
            data={terminalData}
            columns={[
              {
                align: "left",
                className: "test",
                ellipsis: true,
                colKey: "name",
                title: "设备名称",
              },
              {
                align: "left",
                className: "test",
                ellipsis: true,
                colKey: "provider",
                cell({ row }) {
                  switch (row?.provider) {
                    case "BAIDU":
                      return <>百度地图</>;
                    case "AMAP":
                      return <>高德地图</>;
                    case "QQ":
                      return <>腾讯地图</>;
                    default:
                      return null;
                  }
                },
                title: "服务商",
              },
              {
                align: "left",
                className: "test4",
                ellipsis: true,
                colKey: "id",
                title: "服务ID",
              },
              {
                align: "left",
                className: "test4",
                ellipsis: true,
                colKey: "created",
                cell({ row }) {
                  return moment(row.created).format("YYYY-MM-DD HH:mm:ss");
                },
                title: "创建时间",
              },
              {
                align: "left",
                className: "test4",
                ellipsis: true,
                colKey: "updated",
                cell({ row }) {
                  return moment(row.updated).format("YYYY-MM-DD HH:mm:ss");
                },
                title: "更新时间",
              },
              {
                align: "left",
                className: "test4",
                ellipsis: true,
                colKey: "desc",
                title: "描述",
              },
              {
                colKey: "op",
                width: 100,
                title: "操作",
                align: "center",
                fixed: "right",
                cell(record: any) {
                  return (
                    <>
                      <a
                        className="link"
                        onClick={() => {
                          setDeleteDish(record.row);
                          setVisible(true);
                        }}
                      >
                        删除
                      </a>
                    </>
                  );
                },
              },
            ]}
            rowKey="index"
            verticalAlign="top"
            bordered
            hover
            pagination={{
              defaultCurrent: terminalPage,
              defaultPageSize: terminalPageSize,
              total: terminalTotal,
              showJumper: true,
              onChange(pageInfo) {
                setTerminalPageSize(pageInfo.pageSize);
                setTerminalPage(pageInfo.current);
                getData();
              },
            }}
          ></Table>
        ) : (
          <NoData />
        )}

        <Dialog
          header={
            <>
              <ErrorCircleFilledIcon style={{ color: "#ED7B2F" }} />
              <span>确定要删除这条轨迹吗？</span>
            </>
          }
          footer={
            <>
              <Button
                theme="default"
                variant="outline"
                onClick={() => setVisible(false)}
              >
                取消
              </Button>
              <Button theme="danger" onClick={() => deleteHandle()}>
                删除
              </Button>
            </>
          }
          visible={visible}
          onClose={handleFooterClose}
        ></Dialog>
      </div>
    </>
  );
}

export default Terminal;
